.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid white;

    .header-div {
      color: white;
      text-transform: capitalize;
      font-size: 18px;
      font-weight: bold;
      width: 23%;

      &:last-child {
        width: 9%;
      }
    }
  }

  .total {
    color: white;
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }

  .testing {
    text-align: center;
    margin-top: 40px;
    font-size: 20px;
    color:rgb(249, 249, 249);
  }

  button {
    margin-left: auto;
    margin-top: 50px;
  }
}
